
import {
  defineComponent, reactive, computed, ref, toRefs, onMounted, inject, watch,
} from 'vue';
import { Search } from '@element-plus/icons-vue';

export default defineComponent({
  name: 'webpage-builder_collapse-search-filter',
  props: {
    handleChange: {
      type: Function,
      required: true,
    },
  },
  emits: ['search'],
  setup(props, { emit }) {
    const state = reactive({
      isCollapsed: true,
      searchParams: {
        keywords: '',
        status: '',
      },
    });


    const handleSearch = () => {
      const params = {
        ...state.searchParams,
      };
      // props.handleChange(params);
      state.isCollapsed = true;
    };

    const handleReset = () => {
      handleSearch();
    };


    onMounted(async () => {
      await handleSearch();
    });

    return {
      ...toRefs(state),
      Search,
      handleSearch,
      handleReset,
    };
  },
});
