
import {
  defineComponent, reactive, toRef, toRefs, ref, watch, onMounted, PropType,
} from 'vue';
import VueCropper, { cropper } from 'vue-picture-cropper';
import Upload from '@/components/upload/index.vue';
import validFile from '@/utils/valid-file';

export type Options = { types?: string[], timeout?: number, folder?: boolean, noCancel?: boolean, multiple?: boolean };

export type TypeValidationOption = {
  title?: string
  extensions?: string[]
  sizeLimit?: number
  mimeTypes?: string[]
};
export default defineComponent({
  components: {
    VueCropper,
    Upload,
  },
  props: {
    keyword: {
      type: String,
      default: () => '图片',
    },
    dialogTitle: {
      type: String,
      default: () => '裁剪图片',
    },
    pathType: {
      type: String,
      default: 'USER_IMG',
    },
    options: {
      type: Object as PropType<Options>,
    },
    validOptions: {
      type: Object as PropType<Parameters<typeof validFile>[1]>,
    },
  },
  dialogify: {
    props: {
      title: '',
      width: '540px',
    },
  },
  emits: ['cancel', 'confirm'],
  setup(props, { attrs, emit }) {
    const cropperOptions = {
      viewMode: 1,
      dragMode: 'crop',
      aspectRatio: 1,
      restore: true,
      ...attrs,
    };
    const imageUrl = ref<string>('');

    const cancelForm = async () => {
      emit('cancel');
    };

    const submitForm = async () => {
      const blobUrl = cropper.getDataURL();
      // 上传到OSS
      emit('confirm', {
        url: blobUrl,
        blobUrl,
      });
    };

    return {
      submitForm,
      cancelForm,
      imageUrl,
      cropperOptions,
    };
  },
});
